import { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { fetchSearchResults2 } from "../../utils/SearchUtils";
// @ts-ignore
import config from "../../config.json";

const Suggestions = ({ query, searchResults, setSearchResults, setLoading }) => {
    const [suggestions, setSuggestions] = useState([])

    useEffect(() => {
        if (!(query.length > 0)) return

        try {
            setLoading(true)
            fetchSearchResults2(query).then(results => {
                if (!results.length) {
                    closeSuggestions()
                    return;
                }
                setSuggestions(results)
                document.getElementById('searchBar').classList.add("open");
            })

        } catch (error) {
            if (config.DEBUG) console.log(error)
        } finally {
            setTimeout(() => {
                setLoading(false)
            }, 2000);
        }

        return () => {
            closeSuggestions()
        }
    }, [query, setLoading])


    const closeSuggestions = () => {
        document.getElementById('searchBar').classList.remove("open");
        setSuggestions([])
    }

    // close suggestions on Escape key
    document.onkeyup = (e) => {
        if (e.key === "Escape") closeSuggestions();
    };

    useEffect(() => {
        if (searchResults.length > 0) {
            document.getElementById('searchBar').classList.remove("open");
            setSuggestions([])
        }
    }, [searchResults])

    const handleSuggestionClick = async (suggestion) => {
        const results = await fetchSearchResults2(suggestion)
        setSearchResults(results)
    }


    if (suggestions.length > 0) {
        return (
            <div id="suggestionBox" style={{ display: "block" }}>
                <div id="divider"></div>
                <ul id="suggestionsList">
                    {suggestions.map(s => {
                        return <li
                            className="suggestion"
                            key={s.pk}
                            onClick={() => handleSuggestionClick(s.phrase)}
                        >
                            <input className="suggestionKeyInput" type="radio" name={s.phrase} id={s.pk} />
                            <label className="suggestionKey" htmlFor={s.pk}>{s.phrase}</label>
                            <div className="suggestionClickButton">
                                <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                                    <path d="m23.772 10.462-8.5-8.25c-.216-.21-.539-.269-.814-.153-.277.118-.458.39-.458.691v4.252c-7.743.134-14 6.474-14 14.248 0 .342.241.622.572.708.059.015.118.022.177.022.274 0 .541-.157.678-.404 2.245-4.056 6.519-6.576 11.155-6.576h1.418v4.25c0 .301.181.573.458.691.274.116.599.057.814-.153l8.5-8.25c.146-.141.228-.335.228-.538s-.082-.397-.228-.538z"></path>
                                </svg>
                            </div>
                        </li>
                    })}
                </ul>
            </div>
        )
    } else {
        return null
    }
}

Suggestions.propTypes = {
    query: PropTypes.string,
    searchResults: PropTypes.array,
    setSearchResults: PropTypes.func,
    setLoading: PropTypes.func

}

export default Suggestions
