import PropTypes from "prop-types";

function SearchResults({ searchResults }) {
	return (
		<section id="results">
			{searchResults.map((e, index) => (
				<div className="translation_block" key={index}>
					<div className="phraseFamily">
						<h4 className="phrase">{e.phrase}</h4>
						<p className="family">{e.family}</p>
					</div>
					<p className="translation">{e.translation}</p>
				</div>
			))}
		</section>
	);
}

// Proptypes for results and searchTerm
SearchResults.propTypes = {
	results: PropTypes.array,
	searchTerm: PropTypes.string,
};

export default SearchResults;
