import React, { useState } from 'react'
import SearchInputBar from "./SearchInputBar";
import SearchResults from "./SearchResults";
// import Suggestions from "./Suggestions";
import RelatedWords from "./RelatedWords";

// import PropTypes from 'prop-types'


function Search(props) {
    const [query, setQuery] = useState('')
    const [searchResults, setSearchResults] = useState([])
    // const [suggestions, setSuggestions] = useState([])

    return (
        <React.Fragment>
            <SearchInputBar query={query} setQuery={setQuery} searchResults={searchResults} setSearchResults={setSearchResults}/>
            <RelatedWords   query={query} setQuery={setQuery} searchResults={searchResults} setSearchResults={setSearchResults}/>
            {/* <Suggestions query={query} searchResults={searchResults} setSearchResults={setSearchResults}/> */}
            <SearchResults searchResults={searchResults} />
        </React.Fragment>
    )
}

Search.propTypes = {

}

export default Search

