import { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import { fetchRelatedWords } from "../../utils/SearchUtils";
import "../../css/RelatedWords.css"
import { CustomSpinner } from "../Spinner";


const RelatedWords = ({ searchResults, query, setQuery, setSearchResults }) => {
    const [relatedWords, setRelatedWords] = useState([]);
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        if (searchResults.length > 0) {
            setLoading(true)
            fetchRelatedWords(query).then(res => {
                if (res.length > 0) {
                    // filter out the words that are already in the search results
                    const filtered = res.filter(r => {
                        return searchResults.every(s => {
                            return s.pk !== r.pk
                        })
                    })
                    setRelatedWords(filtered);
                }
            }).finally(() => setLoading(false))
        }

        return () => {
            setRelatedWords([]);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchResults]);


    if (relatedWords.length > 0) {
        return (
            <ul className="related-words">
                {relatedWords.map(word => <Word key={word.pk} word={word} setSearchResults={setSearchResults} />
                )}

            </ul>
        )
    } else {
        if (loading) return <ul className="related-words spinner">
            <CustomSpinner loading={loading} size={40}/>
        </ul>
        else return null
    }
}

RelatedWords.propTypes = {
    searchResults: PropTypes.array,
    query: PropTypes.string
}


const Word = ({setSearchResults, word}) => {
    
    const updateSearchResults = () => {
        setSearchResults(searchResults => [{ ...word }, ...searchResults])
    }

    return <li onClick={updateSearchResults}>{word.phrase}</li>
}


export default RelatedWords
