import React from "react";
import PropTypes from "prop-types";
import "../css/spinner.css";

const getBalls = ({ countBalls, color, size, sizeUnit }) => {
	const balls = [];
	for (let i = 0; i < countBalls; i++) {
		balls.push(
			<div
				className="ball"
				style={{
                    width: `${i * (size / countBalls)}${sizeUnit}`,
                    height: `${i * (size / countBalls)}${sizeUnit}`,
                    borderTopColor: color,
                    animationDelay: `${i * 0.05}s`,
                }}
				color={color}
				size={size}
				key={i.toString()}
				index={i}
			></div>
		);
	}
	return balls;
};

export const CustomSpinner = ({ size, color, loading, sizeUnit }) => {
	const countBalls = size / 5;
	return (
		loading && (
			<div
				className="spinner_wrapper"
				style={{
                    width: `${size}${sizeUnit}`,
	                height: `${size}${sizeUnit}`,
                }}
				size={size}
			>
				{getBalls({
					countBalls,
					color,
					size,
					sizeUnit,
				})}
			</div>
		)
	);
};

CustomSpinner.defaultProps = {
	loading: true,
	size: 22,
	color: "var(--secondary-color)",
	sizeUnit: "px",
};

CustomSpinner.propTypes = {
	loading: PropTypes.bool,
	size: PropTypes.number,
	color: PropTypes.string,
	sizeunit: PropTypes.string,
};
