
import config from "../config.json";

const BACKEND = config.SERVER_URL

const fetchSearchResults = async (q) => {
	const url = `${BACKEND}?q=${q}`;
	try {
		const results = await fetch(url);
		const res1 = await results.json();
		if (results.status === 429) {
			const secs = res1.detail.match(/\d+/g);
			return [
				{
					translation: `Serverga juda ko'p so'rov yuborganiz uchun to'xtatildiz. ${Number(secs)} soniyadan keyin urinib ko'ring.`,
				},
			];
		} else return res1;
	} catch (error) {
		console.warn("Search throttled, slow down mate.");
		return null;
	}
};

